import React from 'react'
import styled from 'styled-components'
import wewear from './WeWear.png'
import yourdoc from './YourDoc.png'
import upstyle from './Upstyle.png'
import stay from './StayUpdate.png'


const ProjectList = [
  {
    id:1,
    img:wewear,
    title: "WE Wear (ASOS.COM clone)",
    description:
    "WE Wear is an e-commerce clothing website that draws inspiration from the popular platform ASOS.com. With a focus on delivering a seamless online shopping experience, the website incorporates essential features such as user authentication, robust search capabilities, efficient filtering options, a convenient sorting mechanism, and a streamlined checkout page.",
    
    tech_stack: ["HTML5", "CSS3", "JavaScript"],
    github_url: "https://github.com/Biswajit2595/impartial-honey-6054",
    demo_url: "https://stunning-frangipane-07bc9c.netlify.app/",
  },
  {
    id:2,
    img: yourdoc,
    title: "Your Doc",
    description:
      "YourDoc is a web application designed to streamline the process of booking doctor's appointments. With a focus on user convenience and efficient healthcare management, YourDoc allows users to easily find and schedule appointments with doctors of their choice. ",
      
    tech_stack: ["Html", "Css", "Javascript", "React", "Chakra UI"],
    github_url: "https://github.com/Biswajit2595/earthy-smell-701",
    demo_url:
      "https://radiant-sprinkles-ed694a.netlify.app/",
  },
  {
    id:3,
    img: stay,
    title: "StayUpdate",
    description:
      "StayUpdate is a news article/current affairs reading application that provides users with up-to-date information and articles. It features user authentication, sorting, filtering, a payment page, and a search bar. My role involved developing the Article Landing Page, implementing filtering, designing a responsive UI, and integrating a search bar.",
      
    tech_stack: ["HTML5", "CSS3", "JavaScript"],
    github_url: "https://github.com/Biswajit2595/auspicious-oven-3200",
    demo_url:
      "https://starlit-basbousa-e2dce5.netlify.app/",
  },
  {
    id:4,
    img: upstyle,
    title: "UPSTYLE",
    description:
      "Elevate your wardrobe with premium clothing and timeless fashion. Discover curated collections that embody elegance and embrace the essence of style. Unleash your fashion potential today!",
      
    tech_stack: ["React","Redux", "Chakra UI", "TypeScript","RestApi"],
    github_url: "https://github.com/Biswajit2595/warlike-current-5989",
    demo_url:
      "https://upstylenew.netlify.app/",
  },


];

const Projects = () => {
  return (
    <div id="projects">
            {
              ProjectList.map((list,index)=>(
                <Card key={index} className="project-card" >
          <CardLeft className="cardL">
            <img src={list.img} alt={list.name} />
          </CardLeft>
          <CardRight>
            <h4 className="project-title" >{list.title}</h4>
            <p className="project-description">{list.description}</p>
            <Stack>
              <span className="stackTitle">Tech Stack -</span>
              {
                list.tech_stack.map(el=>(
                  <span className="tags project-tech-stack" >{el}</span>
                ))
              }
            </Stack>
            <BtnGroup>
              <a
                className="project-github-link"
                href={list.github_url}
                target="_blank"
                rel="noopener noreferrer"
              >
                Github
              </a>
              <a
                className="project-deployed-link"
                href={list.demo_url}
                target="_blank"
                rel="noopener noreferrer"
              >
                Live 
              </a>
            </BtnGroup>
          </CardRight>
        </Card>
              ))
            }
          </div>
  )
}

export default Projects


