// `

const Card = styled.div`
  display: grid;
  width: 90%;
  margin: auto;
  grid-gap: 2rem;
  margin-bottom: 4rem;
  grid-template-columns: 1fr;
  border-bottom: 1px solid black;
  padding-bottom: 2rem;
  border-radius: 1rem;
  background-color: beige;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  @media (min-width: 992px) {
    grid-template-columns: 1fr 1fr;
    font-size: small;
    border-bottom: 0;
    padding-bottom: 0;
  }
`;

const CardLeft = styled.div`
  background: #151418;
  border-radius: 5px;
  /* padding: 5px; */
  justify-self: center;
  background-color: white;
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius:20px;
  
  img {
    border-radius:20px;
    height: 100%;
  }
  img:hover{
    transform: scale(1.05);
  transition: .5s ease-in-out;
  }
`;

const CardRight = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h4 {
    color: #000;
    font-size: 1.5rem;
    font-weight: 400;
  }

  p {
    /* border: 2px solid; */
    margin: 15px;
    font-weight: 400;
    max-width: 400px;
    margin-top: 10px;
    margin-bottom: 1rem;
    color: rgba(0, 0, 0, 0.815);
    text-align: justify;

    @media (min-width: 992px) {
      text-align: start;
      /* font-weight: 300;
      padding: 10px; */
    } 
  }
  @media (min-width: 992px) {
    align-items: flex-start;
    margin-top: 1rem;
  }
`;

const Stack = styled.div`
  display: flex;
  margin-bottom: 5px;
  flex-wrap: wrap;

  .stackTitle {
    color: #000;
    font-weight: 500;
    font-size: 17px;
  }

  .tags {
    color: #000;
    display: flex;
    background-color: #46808f;
    border-radius: 5px;
    font-size: 15px;
    font-weight: 400;
    margin-left: 5px;
  }
  .tags:hover{
    transition:transform 1s;
    cursor:pointer;
    transform:scale(1.1);
  }
`;

const BtnGroup = styled.div`
  height: 70px;
  display: flex;
  align-items: center;
  color: black;
  a{
    margin: 10px;
    background-color: #46808f;
    padding: 5px;
    width: 100px;
    border-radius: .5rem;
  }
    .btn:hover{
      transition:transform 1s;
    cursor:pointer;
    transform:scale(1.2);
    }
  
`;
