import React from 'react'
import styled from 'styled-components';

const Contact = () => {
  return (
    <CONTACT className="content" id="contact">
    <div id='info_contact'>
      <h3>Biswajit Roy</h3>
      <p id="contact-email">Email: broy16431@gmail.com</p>
      <p  id="contact-phone">Phone: +91 9641792908</p>
      <p>Location: Jalpaiguri, West Bengal</p>
      <button>Resume</button>
    </div>
    <div>
      <h3>Socials</h3>
      <div id="Social">
        <a  id="contact-linkedin" href="https://www.linkedin.com/in/biswajitroy95/" rel="noreferrer" target="_blank">
          <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/c/ca/LinkedIn_logo_initials.png/640px-LinkedIn_logo_initials.png" alt="LinkedIn" />
        </a>
        <a id="contact-github" href="https://github.com/Biswajit2595" rel="noreferrer" target="_blank">
          <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/3/3f/Github-circle_%28CoreUI_Icons_v1.0.0%29.svg/2048px-Github-circle_%28CoreUI_Icons_v1.0.0%29.svg.png" alt="Github" />
        </a>
      </div>
    </div>
  </CONTACT>
  )
}

export default Contact

const CONTACT = styled.div`
  /* width: 80%; */
  margin: auto;
  display: grid;
  background-color:#46808f ;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  justify-content: space-between;
  padding: 10px;

  @media (max-width: 767px) {
    grid-template-columns: 1fr;
    gap: 1rem;
    text-align: center;
  }

  div {
    @media (max-width: 767px) {
      text-align: center;
    }
  }

  h3 {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }

  p {
    font-size: 1rem;
    margin: 0.5rem 0;
  }
  #info_contact{
    margin: auto;
    /* border: 2px solid; */
    width: 50%;
  }
  #info_contact>p{
    text-align: center;
  }

  #Social {
    display: flex;
    justify-content: center;
    gap: 1rem;

    a {
      width: 40px;
      height: 40px;
      display: inline-block;
      border-radius: 50%;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
`;
