import React from 'react'
import styled from 'styled-components';

const Skills = () => {
  return (
    <SKILLS className='content' id="skills" >
          <div className="skills-card">
            <img src='https://mohitkss.github.io/static/media/html5.4e6edfe05f07c383e94f.png' className="skills-card-img" alt='HTML'/>
            <p className="skills-card-name">HTML5</p>
          </div>
          <div className="skills-card">
            <img className="skills-card-img"  src='https://mohitkss.github.io/static/media/css3.845e8eb63836bef093cf.png' alt='CSS3'/>
            <p className="skills-card-name">CSS3</p>
          </div>
          <div className="skills-card">
            <img className="skills-card-img"  src='https://mohitkss.github.io/static/media/js.3dfb665e058d08c99f28.png' alt='JS'/>
            <p className="skills-card-name">JAVASCRIPT</p>
          </div>
          <div className="skills-card">
            <img className="skills-card-img"  src='https://mohitkss.github.io/static/media/git.b1472a80b81e487179cf.png' alt='github'/>
            <p className="skills-card-name">GIT/GITHUB</p>
          </div>
          <div className="skills-card">
            <img className="skills-card-img"  src='https://mohitkss.github.io/static/media/mongodb.4f7af09e6354d51beec8.png' alt='MONGODB'/>
            <p className="skills-card-name">MONGODB</p>
          </div>
          <div className="skills-card">
            <img className="skills-card-img"  src='https://mohitkss.github.io/static/media/nodejs.b508473ad71a31ce2fae.png' alt='NODEJS'/>
            <p className="skills-card-name">Nodejs</p>
          </div>
          <div className="skills-card">
            <img className="skills-card-img"  src='https://mohitkss.github.io/static/media/react.22d1f2096ed82cab7a8a.png' alt='REACT'/>
            <p className="skills-card-name">REACT</p>
          </div>
          <div className="skills-card">
            <img className="skills-card-img"  src='https://mohitkss.github.io/static/media/redux.7fec6369cecd1cbd44d6.png' alt='REDUX'/>
            <p className="skills-card-name">REDUX</p>
          </div>
          <div className="skills-card">
            <img className="skills-card-img"  src='https://mohitkss.github.io/static/media/mern.b58d81727b47466a7cda.png' alt='MERN'/>
            <p className="skills-card-name">MERN</p>
          </div>
          <div className="skills-card">
            <img className="skills-card-img" src='https://upload.wikimedia.org/wikipedia/commons/4/4c/Typescript_logo_2020.svg' alt='TYPESCRIPT'/>
            <p className="skills-card-name">TYPESCRIPT</p>
          </div>
        </SKILLS>
  )
}

export default Skills

const SKILLS=styled.div`
/* border: 2px solid black; */
width: 80%;
background-color: #46808f;
padding: 1.5rem;
margin: auto;
display: grid;
grid-template-columns: repeat(auto-fit, minmax(120px, max-content));
gap: 1rem;
border-radius: 2rem;

div{
  color: black;
  margin: auto;
  padding: 1rem;
  background-color: #f4f5e7;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 5px;
}
div:hover{
  transform: scale(1.1);
  transition: .5s ease-in-out;
}

img{
  width: 100px;
}
`;