import React from 'react'
import styled from 'styled-components';

const About = () => {
  return (
    <ABOUT className='about section' id="about" >
        <div textAlign={'center'} justifyContent={'center'} >
      <h1>ABOUT ME</h1>
      <div id="about">
      <div>
      <img src='https://camo.githubusercontent.com/8bf6f6d78abc81fcf9c49f10649423e73ea44bc248e83aaae8759d401c829a84/68747470733a2f2f70687973696373677572756b756c2e66696c65732e776f726470726573732e636f6d2f323031392f30322f6368617261637465722d312e676966' alt='aboutgif'/>
      </div>
      <div id="user-detail-intro">
      <h3>
      Results-driven and Ambitious full stack web developer proficient in HTML, CSS, JavaScript, and React. Completed a rigorous 1200+ hour web development course at Masai School, showcasing strong problem-solving skills and team collaboration. Eager to embrace emerging technologies and contribute to the industry's advancement.
      </h3>
      </div>
    </div>
      </div>
        </ABOUT>
  )
}

export default About

const ABOUT = styled.div`
  width: 70%;
  color: #000;
  margin: auto;
  padding: 1.5rem;
  background-color: #46808f;
  border-radius: 2rem;

  h1 {
    font-size: larger;
    font-weight: 700;
  }

  #inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: auto;
    img {
      margin: auto;
      margin-top: 30px;
      width: 70%;
      border-radius: 2rem;
    }

    #about_text {
      padding: 1rem;
      width: 95%;
      font-family: sans-serif;
      font-size: larger;
      text-align: justify;
    }

    @media (max-width: 991px) {
      grid-template-columns: repeat(1, 1fr);
      justify-items: center;
    }
  }
`;