import React from 'react'
import styled from 'styled-components';
import resume from './Biswajit-Roy-Resume.pdf'
import photo from './My_project-no-background.png'
import { FiDownload,FiEye } from "react-icons/fi";
import { TypeAnimation } from 'react-type-animation';

const Home = () => {
  return (
    <DIV className='content' id='home' >
          <div id='text' style={{textAlign:"start"}}>
            <div id="innertext">
            <h2 >Hi,</h2>
            <h2 id="user-detail-name">I'm Biswajit Roy</h2>
            <div id="user-detail-intro">
            <TypeAnimation  
  sequence={[
    // Same substring at the start will only be typed once, initially
    'I am a Full Stack Web Developer',
    1000,
    'I am a Problem Solver',
    1000,
    'I am a Continuous Learner',
    1000,
  ]}
  speed={20}
  style={{ fontSize: '1.5rem' }}
  repeat={Infinity}
/>
</div>
<h4>I am a skilled full-stack web developer, proficient in both front-end and back-end technologies, dedicated to creating engaging and user-friendly web experiences.</h4>
</div>
          
          <div id='resume'>
          <a href={resume} className={"nav-link resume"} rel="noreferrer" target="_blank" download>
        <button id="resume-button-2" > Resume<FiDownload/> </button>
        </a>
        {/* <a href="https://drive.google.com/file/d/1UgPSvGcnKWP54KctzuAUH5RChTajffl1/view?usp=sharing" className={"nav-link resume"} rel="noreferrer" target="_blank">
        <button id="resume-button-2"> Resume <FiEye/></button>
        </a> */}
          </div>
        </div>
        
        <div id='image'>
          <img className="home-img" src={photo}  alt='Biswajit Roy'/>
        </div>
        </DIV>
  )
}

export default Home

const DIV = styled.div`
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  justify-content: space-between;
  padding: 10px;

  #resume {
    width:100%;
    display:flex;
    gap:20px;
    justify-content: center;
  }

  #resume a button{
    background-color: #46808f;
    /* font-size: small; */
    color: #f2f2f3;
    display: flex;
    padding: 10px;
    height:40px;
    cursor:pointer;
    border-radius:10px;
    align-items: center;
    gap: 5px;
  }

  #text {
    /* align-items: center; */
    margin-top: 40px;
    color: black;

  }


  img {
    width: 80%;
    border-radius: 50%;
    margin: auto;
  }

  @media (max-width: 991px) {
    grid-template-columns: repeat(1, 1fr);
    /* justify-items: center; */
    text-align: center;

    img {
      width: 60%;
    }
  }
`;